<template>
  <div class="chat-app container mt-5" :class="{ 'center-room': !inRoom }">
    <div v-if="!inRoom" class="room-container d-flex justify-content-center align-items-center">
      <div class="w-100 text-center">
        <h2 class="mb-4">ËÅäÂ§©ÂÆ§</h2>

        <!-- Main Chatroom and Dropdown Section -->
        <div class="row justify-content-center mb-4">
          <div class="col-12 col-md-6 mb-3">
            <button class="btn btn-dark w-75" @click="roomName = 'Ê≠°Ê®ÇËÅäÂ§©ÂÆ§'; joinRoom()">Ê≠°Ê®ÇËÅäÂ§©ÂÆ§</button>
          </div>

          <div class="col-12 col-md-6 mb-3">
            <div class="input-group">
              <select v-model="selectedRoom" class="form-select">
                <option disabled value="">ÈÅ∏Êìá‰ºÅÊ•≠ËÅäÂ§©ÂÆ§</option>
                <option value="ÊÅ©Êô∫Êµ¶ÂçäÂ∞éÈ´î">ÊÅ©Êô∫Êµ¶ÂçäÂ∞éÈ´î</option>
                <option value="LINEÂè∞ÁÅ£">LINEÂè∞ÁÅ£</option>
                <option value="Google">Google</option>
                <option value="Âè∞Á©çÈõª">Âè∞Á©çÈõª</option>
                <option value="ÁæÖÊäÄ">ÁæÖÊäÄ</option>
                <option value="‰∏≠ËèØÈõª‰ø°">‰∏≠ËèØÈõª‰ø°</option>
                <option value="ÂâµÂÆ¢‰∫§ÊµÅÁµÑ">ÂâµÂÆ¢‰∫§ÊµÅÁµÑ</option>
              </select>
              <button class="btn btn-secondary" @click="confirmRoomSelection">Á¢∫Ë™ç</button>
            </div>
          </div>
        </div>

        <!-- Custom Room Creation Section -->
        <div class="custom-room mt-4">
          <h4>ÁßÅ‰∫∫ËÅäÂ§©ÂÆ§</h4>
          <div class="d-flex justify-content-center mt-3">
            <div class="input-group custom-input-group">
              <input v-model="roomName" type="text" class="form-control" placeholder="ËÅäÂ§©ÂÆ§ÂêçÁ®±" />
              <button class="btn btn-secondary" @click="joinRoom">Âä†ÂÖ•</button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-else>
      <div class="card">
        <div class="card-header bg-dark text-white d-flex justify-content-between align-items-center">
          <h5>{{ roomName }}</h5>
          <button class="btn btn-outline-light" @click="handleRoomExit">Èõ¢Èñã</button>
        </div>
        <div class="card-body">
          <ChatWindow
            :userName="userName"
            :roomName="roomName"
            @left-room="handleRoomExit"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createChatRoom } from "@/services/chatroom/chatroomService";
import ChatWindow from "./ChatWindow.vue";

export default {
  name: "ChatApp",
  components: {
    ChatWindow,
  },
  data() {
    return {
      userName: localStorage.getItem("userName") || "",
      roomName: "",
      selectedRoom: "",
      inRoom: false,
    };
  },
  methods: {
    async joinRoom() {
      if (!this.roomName.trim()) {
        alert("Please select or enter a room name.");
        return;
      }
      try {
        await createChatRoom(this.roomName);
        this.inRoom = true;
        localStorage.setItem("roomName", this.roomName);
      } catch (error) {
        alert(error.message || "Failed to join or create room.");
      }
    },
    confirmRoomSelection() {
      if (!this.selectedRoom) {
        alert("Please select a room from the dropdown.");
        return;
      }
      this.roomName = this.selectedRoom;
      this.joinRoom();
    },
    handleRoomExit() {
      this.roomName = "";
      this.inRoom = false;
      localStorage.removeItem("roomName");
    },
    logout() {
      localStorage.removeItem("userName");
      localStorage.removeItem("roomName");
      this.$router.push("/login");
    },
  },
  mounted() {
    const savedRoomName = localStorage.getItem("roomName");
    if (savedRoomName) {
      this.roomName = savedRoomName;
      this.inRoom = true;
    }
  },
};
</script>

<style scoped>
