/* Ë™øÊï¥ÊåâÈàïÂ§ßÂ∞èÂíåÈñìË∑ù‰ª•‰øùÊåÅ‰∏ÄËá¥ */
.btn-secondary {
  background-color: #6c757d;
  border-color: #6c757d;
}

.btn-secondary:hover {
  background-color: #5a6268;
  border-color: #545b62;
}

/* ÈüøÊáâÂºèË™øÊï¥ */
@media (max-width: 768px) {
  .input-group {
