<template>
  <div class="message-board">
    <!-- È°ØÁ§∫ÊâÄÊúâÁïôË®Ä -->
    <div v-for="(note, index) in sortedNotes" :key="index" class="message-card">
      <p class="message-content">{{ note.content }} </p>
      <p class="message-time">{{ formatTime(note.time) }}</p>
      
      <!-- ÊåâËÆöÂäüËÉΩ -->
      <div class="like-section">
        <button @click="likeNote(note.note_id)">üëç {{ note.likes }} Likes</button>
      </div>
    </div>
    
    <!-- Âè≥‰∏ãËßíÂúìÂΩ¢Âä†ËôüÊåâÈàï -->
    <button class="add-note-button" @click="showModal = true">+</button>
    
    <!-- Ê®°ÊÖãË¶ñÁ™ó (Modal) -->
    <div v-if="showModal" class="modal-overlay">
      <div class="modal">
        <textarea v-model="newNoteContent" placeholder="Ëº∏ÂÖ•ÁïôË®ÄÂÖßÂÆπ"></textarea>
        <button class="btn-submit" @click="submitNote">Êñ∞Â¢û</button>
        <button class="btn-cancel" @click="showModal = false">ÂèñÊ∂à</button>
      </div>
    </div>
  </div>
</template>

<script>
import { createNote, getAllNotes, updateLikes } from "@/services/noteboard/noteboardService";

export default {
  props: ['isLoggedIn', 'userName'],
  data() {
    return {
      notes: [
      ],              
      showModal: false,        
      newNoteContent: '',     
    };
  },
  computed: {
    sortedNotes() {
      return [...this.notes].sort((a, b) => {
        if (b.likes === a.likes) {
          return new Date(b.time).getTime() - new Date(a.time).getTime();
        }
        return b.likes - a.likes;
      });
    },
  },
  methods: {
    async fetchNotes() {
      try {
        const response = await getAllNotes();
        this.notes = response.notes;  
      } catch (error) {
        console.error(error);
      }
    },
    async submitNote() {
      if (!this.newNoteContent.trim()) {
        alert("Ë´ãËº∏ÂÖ•ÁïôË®ÄÂÖßÂÆπ");
        return;
      }
      const newNote = {
        content: this.newNoteContent,
      };
      try {
        // ÁôºÈÄÅÂà∞ÂæåÁ´ØÊñ∞Â¢ûÁïôË®Ä
        await createNote(newNote.content);
        this.newNoteContent = ''; 
        this.showModal = false;   
        this.fetchNotes();      
      } catch (error) {
        alert(error.message || "Êñ∞Â¢ûÁïôË®ÄÂ§±Êïó");
      }
    },
    async likeNote(noteId) {
      // ÊâæÂà∞Â∞çÊáâÁöÑÁïôË®Ä‰∏¶Â¢ûÂä†ËÆöÊï∏
      const note = this.notes.find((n) => n.note_id === noteId);
      if (note) {
        note.likes += 1;
        try {
          // ÁôºÈÄÅË´ãÊ±ÇÂà∞ÂæåÁ´ØÊõ¥Êñ∞ÊåâËÆöÊï∏
          await updateLikes(noteId, note.likes);
        } catch (error) {
          console.error("Êõ¥Êñ∞ÊåâËÆöÊï∏Â§±Êïó", error);
        }
      }
    },
    formatTime(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleString();
    },
  },
  mounted() {
    this.fetchNotes();
    if (!this.userName) {
      this.$router.push('/chatapp').then(() => {
        window.location.reload(); // ÈáçÂÆöÂêëÂæåÈáçÊñ∞Êï¥ÁêÜÈ†ÅÈù¢
      });
    }
  },
};
</script>

<style scoped>
/* ÁïôË®ÄÊùøÂÆπÂô® */
.message-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5% 3% 3% 5%;
  gap: 1.5vw;
}

/* ÁïôË®ÄÂç°Áâá */
.message-card {
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  width: 90vw;   /* Âõ∫ÂÆöÂØ¨Â∫¶ */
  max-width: 500px;
  padding: 1.5vw;
  border-radius: 10px; /* ÂúìËßí */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  word-wrap: break-word;  /* ËÆìÂÖßÂÆπËá™ÂãïÊèõË°å */
}
.message-card:hover{
  background-color: #d1d1d1;
}

/* ÁïôË®ÄÂÖßÂÆπ */
.message-content {
  font-size: 1.1em;
  margin-bottom: 10px;
}

/* ÁïôË®ÄÊôÇÈñì */
.message-time {
  font-size: 0.9em;
  color: #888;
  text-align: right;
}

/* ÊåâËÆöÂçÄÂ°ä */
.like-section {
  margin-top: 10px;
  text-align: right;
}

/* ÊåâËÆöÊåâÈàï */
.like-section button {
  background-color: transparent;
  border: none;
  color: #f39c12;
  font-size: 1.1em;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}

.like-section button:hover {
  color: #e67e22;
}

/* Êñ∞Â¢ûÁïôË®ÄÊåâÈàï */
.add-note-button {
  position: fixed;
  bottom: 2vw;
  right: 2vw;
  background-color: #f39c12;
  border: none;
  color: white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 24px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

/* Ê®°ÊÖãË¶ñÁ™óËÉåÊôØ */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Ê®°ÊÖãË¶ñÁ™ó */
.modal {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
}

textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 16px;
  margin-bottom: 10px;
}
.btn-submit {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #e0e0e0; 
  color: #333;
  font-size: 16px;
  cursor: pointer;
  margin-right: 20px;
}

.btn-cancel {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color:#e0e0e0; 
  color: #333;
  font-size: 16px;
  cursor: pointer;
}

.btn-submit:hover, .btn-cancel:hover {
  background-color:#d1d1d1;
}
</style>